*{box-sizing:border-box}body{background-color:#713c31}@-webkit-keyframes headerintro{0%{opacity:0;-webkit-transform:translateY(25%);transform:translateY(25%)}to{opacity:1;-webkit-transform:translateY(0);transform:translateY(0)}}@keyframes headerintro{0%{opacity:0;-webkit-transform:translateY(25%);transform:translateY(25%)}to{opacity:1;-webkit-transform:translateY(0);transform:translateY(0)}}@-webkit-keyframes navintro{0%{opacity:0}to{opacity:1}}@keyframes navintro{0%{opacity:0}to{opacity:1}}.c-header{font-family:Playfair Display,sans-serif;padding:1.5em 1em 0;-webkit-animation:headerintro .9s;animation:headerintro .9s;-webkit-animation-delay:.5s;animation-delay:.5s;-webkit-animation-fill-mode:backwards;animation-fill-mode:backwards}.c-header__title{font-size:3.5rem;margin:0 0 .4em;letter-spacing:2px}.c-header__description{font-size:1.1rem;margin:0 0 1em}@media screen and (min-width:20rem){.c-header__title{font-size:calc(3.5rem + (250vw - 50rem)/50)}.c-header__description{font-size:calc(1.1rem + (90vw - 18rem)/50)}}@media screen and (min-width:70rem){.c-header__title{font-size:6rem}.c-header__description{font-size:2rem}}.c-miniatures{list-style:none;padding:0;flex-grow:1;display:flex;flex-direction:column;justify-content:space-around;-webkit-animation:navintro .9s;animation:navintro .9s;-webkit-animation-delay:1.4s;animation-delay:1.4s;-webkit-animation-fill-mode:backwards;animation-fill-mode:backwards}.c-miniatures__miniature{border-radius:2em;margin:.4em .5em;position:relative}.c-miniatures__link{display:block;padding:.23em 0;color:#fff8cf;text-decoration:none;font-size:2.6rem;font-weight:700;cursor:pointer;z-index:0}.c-miniatures__miniature:after{content:"";position:absolute;border-radius:2em;top:0;left:0;width:100%;height:100%;box-shadow:0 0 2em 0 rgba(0,0,0,.75);opacity:0;transition:.1s;z-index:-1}.c-miniatures__miniature:hover:after{opacity:.5}.c-miniatures__link:active,.c-miniatures__link:focus{outline:10px dotted #000}@media screen and (min-width:20rem){.c-miniatures__link{font-size:calc(1.5rem + (110vw - 22rem)/50)}}@media screen and (min-width:70rem){.c-miniatures__link{font-size:2.6rem}}@media screen and (orientation:landscape){.c-miniatures{flex-direction:row;flex-wrap:wrap;align-items:center}.c-miniatures__miniature{flex-basis:45%}}.c-relic{border-bottom:3px solid #fff}.c-relic:last-child{border-bottom:none}.c-relic--js{position:absolute;top:50%;left:100%;width:100%;-webkit-transform:translateY(-50%);transform:translateY(-50%)}.c-relic__name{font-size:1.4rem}.c-relic__description{font-size:1.2rem;text-align:justify}@media screen and (min-width:20rem){.c-relic__name{font-size:calc(1.4rem + (20vw - 4rem)/50)}.c-relic__description{font-size:calc(1.2rem + (10vw - 2rem)/50)}}@media screen and (min-width:70rem){.c-relic__name{font-size:1.6rem}.c-relic__description{font-size:1.3rem}}.c-footer{-webkit-animation:headerintro .9s;animation:headerintro .9s;-webkit-animation-delay:.5s;animation-delay:.5s;-webkit-animation-fill-mode:backwards;animation-fill-mode:backwards}.c-footer__text{font-size:1.2rem;margin:1.2em 0;font-family:Playfair Display,sans-serif}@media screen and (min-width:20rem){.c-footer__text{font-size:calc(1.2rem + (30vw - 6rem)/50)}}@media screen and (min-width:70rem){.c-footer__text{font-size:1.5rem}}.l-wrap{color:#fff8cf;font-family:Raleway,-apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Oxygen,Ubuntu,Cantarell,Open Sans,Helvetica Neue,sans-serif;text-align:center;max-width:70rem;margin:0 auto;overflow:hidden;position:relative;min-height:100vh;display:flex;flex-direction:column}.l-main-content,.l-main-menu{display:flex;flex-direction:column;flex-grow:1}.l-relics{padding:0 1rem;display:none}